import React, { useState, useEffect } from 'react'
import "./support.less"
import {
    Card,
    Col,
    Input,
    Row,
    Select,
    Table,
    message,
    Upload,
    Button,
    Image,
    Popover,
    Divider,
    Tooltip,
    Progress
} from 'antd'
import { SearchOutlined, UploadOutlined } from '@ant-design/icons';
import baseUrl from "../../utils/base";
import {
    geTickType,
    getHostList,
    getTickInfo,
    replyMssage,
    getDownload
} from "../../api/support";
import moment from "moment";
import { useTranslation } from "react-i18next";
import { useLocation } from 'react-router-dom'
import axios from "axios";
import { order } from "../../api/finance";
const { TextArea } = Input;

export default function Reply() {
    const [orderShow, setorderShow] = useState(true)
    const { t } = useTranslation();
    const location = useLocation()

    const [tickInfo, settickInfo] = useState({})
    const [productList, setproductList] = useState([])
    const [orderTypeList, setorderTypeList] = useState([])
    const [files, setFiles] = useState([])
    const [msg,setmessage]=useState("")
    const [percent,setPercent]=useState(-1)
    let props = {
        accept: '*',
        action: baseUrl+'/upload',
        name: 'file',
        headers: {},
        showUploadList: false,
        multiple:true,
        progress: {
            strokeColor: {
                '0%': '#108ee9',
                '100%': '#87d068',
            },
            strokeWidth: 3,
            format: (percent) => percent && `${parseFloat(percent.toFixed(2))}%`,
        },
        // 删除
        onRemove: ( file ) => {


        },
        //文件上传之前的操作
        beforeUpload: ( file ) => {
            // 设置上传文件大小小于50M
            const isLt10M = file.size / 1024 / 1024 <= 256;
            if (!file.length) {
                if (!isLt10M) {
                    message.error(t("tip.tip6"));
                    clearHtml()
                    return false;
                } else {

                }
            } else {

                return false;
            }
            let flag=false;
            files.forEach((item) => {
                if (item.fullName.split('^')[1] ==file.name) {
                    flag = true;
                }
            })

            if(flag){
                message.error(t("tip.tip5"));
                clearHtml()
                return  false
            }

        },
        onfinish: ( res ) => {
            console.log(res)
        },
        onChange: ( res ) => {
            let judge=true;
            for (let i in res.fileList){
                if(res.fileList[i].percent==100&&res.event){
                }else {
                    judge=false
                }
            }
            if(judge){
                setTimeout(()=>{
                    clearHtml()
                },1000)

            }
            if (res.file.status == 'uploading') {
                return
            }
            if (res.file.status == 'done') {
                console.log(res)
                res.fileList=[];
                if (res.file.response.data == null) {
                    message.error(res.file.response.msg)
                    return
                }
                const data = res.file.response.data;
                let fileSign = 'img';
                let fileName = res.file.response.data.save_name
                if (data.image_base64 == null) {
                    const fileType = data.save_name.split('.');
                    const filenames = data.save_name.split('^');
                    fileName = filenames[1]
                    if (fileType[fileType.length - 1] == 'xlsx' || fileType[fileType.length - 1] == 'xls') {
                        fileSign = 'xls'
                    } else if (fileType[fileType.length - 1] == 'doc' || fileType[fileType.length - 1] == 'docx') {
                        fileSign = 'doc'
                    } else if (fileType[fileType.length - 1] == 'pdf') {
                        fileSign = 'pdf'
                    } else if (fileType[fileType.length - 1] == 'txt') {
                        fileSign = 'txt'
                    } else if (fileType[fileType.length - 1] == 'rar'||fileType[fileType.length - 1] == 'zip') {
                        fileSign = 'rar'
                    }
                }
                const flg = files.findIndex(res => res.name == fileName)
                if (flg == 0) {
                    message.error(t("tip.tip5"));
                    return
                }
                console.log(flg)
                const newData = {
                    name: fileName,
                    fullName: res.file.response.data.save_name,
                    url: res.file.response.data.image_base64,
                    fileSignName: fileSign
                }
                setFiles(() => {
                    return [...files, newData]
                })

            }

        },
    };
    const clearHtml=()=>{
        setTimeout(function () {
            const doms=document.getElementsByClassName("ant-upload-list");
            for (let i = 0; i < doms.length; i++) {
                doms[i].innerHTML=''
            }
        },1000)

    }
    useEffect( () => {
         onGetTickType()
         onGetHostList()
        console.log(location.state.id)
        //onGetickInfo(location.state.id)
    },[]);
    useEffect( () => {
        if(productList.length>0&&orderTypeList.length>0)
        onGetickInfo(location.state.id)
    }, [productList,orderTypeList]);
    const onGetHostList = () => {
        getHostList().then(res => {
            setproductList([...res.data.data.list])
            console.log(res.data.data.list)

        })
    }
    const onGetTickType = () => {
        geTickType().then(res => {
            if (res.data.status == 200) {
                const list = res.data.data.list;
                setorderTypeList([...list])
                console.log(orderTypeList)
            }

        })
    }
    const onGetickInfo = ( id ) => {
        getTickInfo(id).then(res => {
            if (res.data.status == 200) {
                const tickInfo = res.data.data.ticket;
                orderTypeList.map(res => {
                    if (res.id == tickInfo.ticket_type_id) {
                        tickInfo.ticket_type_name = res.name
                    }
                })

                const productNames = []
                productList.map(res => {
                    tickInfo.host_ids.map(res1 => {
                        if (res.id == res1) {
                            productNames.push(res.product_name)
                        }
                    })

                })
                tickInfo.productNames = productNames.join(",")
                const replies=[];
                tickInfo.replies.forEach(res=>{
                    if(res.content!=''||res.attachment.length>0){
                        replies.push(res)
                    }
                })
                tickInfo.replies=replies;
                settickInfo(tickInfo)
            }
        })
    }
    //回复消息
    const onReplyMessage = () => {
        //console.log('1',replyMessage)
        if (msg == '') {
            message.error(t("tip.tip7"))
            return
        }
        const list = [];
        files.map(res => {
            list.push(res.fullName);
        })
        replyMssage(tickInfo.id, {
            content: msg,
            attachment: list
        }).then(res => {
            if (res.data.status == 200) {
                message.success(t("tip.tip8"))
                setmessage("")
                setFiles([])
                //window.location.reload()
                onGetickInfo(tickInfo.id);
            }
        })
    }
    //删除文件
    const removeFile = ( name ) => {
        setFiles(prev => prev.filter(( el ) => el.name !== name));
    }
    //下载文件
    const dowFile = ( res, title ) => {
        const url = res.lastIndexOf("/");
        res=res.substring(url+1,res.length)
        //const url=res.replace('http://101.35.248.14','/api')
        getDownload({
            name:res
        }).then(response => {
            const blob = new Blob([response.data]);
            const fileName = title;
            const linkNode = document.createElement('a');
            linkNode.download = fileName; //a标签的download属性规定下载文件的名称
            linkNode.style.display = 'none';
            linkNode.href = URL.createObjectURL(blob); //生成一个Blob URL
            document.body.appendChild(linkNode);
            linkNode.click();  //模拟在按钮上的一次鼠标单击
            URL.revokeObjectURL(linkNode.href); // 释放URL 对象
            document.body.removeChild(linkNode);
        }).catch(function (error) {
            console.log(error);
        });
    }
  return (
      <div className='support-content'>
      <div className='relpy-model'>
          <Row gutter={30} style={{ width: '100%' }}>
              <Col lg={orderShow ? 8 : 0} md={orderShow ? 24 : 0}>
                  <div className='order-model'>
                      <div className='switch-open' onClick={() => setorderShow(false)}>
                          {t("support.packUp")}
                          <div className='jiantou-left'></div>
                      </div>
                      <div className='title'> {t("support.workInformation")}
                          <div className='status-tag'
                               style={{
                                   background: tickInfo.status == 'Pending' ? '#C9F7F5' : tickInfo.status == 'Handling' ? '#FFF4DE' : tickInfo.status == 'Reply' ? '#FFE2E5' : '#F2F2F7',
                                   color: tickInfo.status == 'Pending' ? '#1BC5BD' : tickInfo.status == 'Handling' ? '#FFA800' : tickInfo.status == 'Reply' ? '#F64E60' : '#9696A3'
                               }}
                          >{tickInfo.status == 'Pending' ?  t("support.pending") : tickInfo.status == 'Handling' ? t("support.handling") : tickInfo.status == 'Reply' ? t("support.reply") : tickInfo.status == 'Replied' ? t("support.replied") : tickInfo.status == 'Resolved' ? t("support.resolved")  : t("support.closed")}</div>
                      </div>
                      <Divider dashed></Divider>
                      <div className='order-info-model'>
                          <div className='left'>{t("support.workTitle")}</div>
                          <div className='right'>{tickInfo.title}</div>
                      </div>
                      <div className='order-info-model'>
                          <div className='left'>{t("support.workType")}</div>
                          <div className='right'>{tickInfo.ticket_type_name}</div>
                      </div>
                      <div className='order-info-model'>
                          <div className='left'>{t("support.relatedProducts")}</div>
                          <div className='right'>{tickInfo.productNames}</div>
                      </div>
                      <div className='order-info-model'>
                          <div className='left'>{t("support.statusNew")}</div>
                          <div
                              className='right'>{tickInfo.status == 'Pending' ?  t("support.pending") : tickInfo.status == 'Handling' ? t("support.handling") : tickInfo.status == 'Reply' ? t("support.reply") : tickInfo.status == 'Replied' ? t("support.replied") : tickInfo.status == 'Resolved' ? t("support.resolved")  : t("support.closed") }</div>
                      </div>
                      <img src={require("@/assets/images/support/custom.png")}/>
                  </div>
              </Col>
              <Col lg={orderShow ? 16 : 24} md={orderShow ? 24 : 0} style={{width:'100%',marginTop:orderShow?'0px':'24px'}}>
                  <div>
                      <div className='replies'>
                          {
                              orderShow == false ?
                                  <div className='switch-open' onClick={() => setorderShow(true)}>
                                      {t("support.open")}
                                      <div className='jiantou-left'></div>
                                  </div> : ''
                          }

                          <div className='title'>{t("support.communicationRecords")}</div>
                          <div className='list'>
                              {
                                  tickInfo.replies != null && tickInfo.replies.map(( item, index ) => {
                                      if (item.type == 'Client') {
                                          return <div className='item' key={index}>
                                              <div className='name'
                                                   style={{ textAlign: 'right' }}>{item.client_name}
                                                  <img src={require("@/assets/images/support/client.png")}/>
                                              </div>
                                              <div className='content'>
                                                  <div className='message'>
                                                      {item.content}
                                                  </div>

                                              </div>
                                              <div className='files'>
                                                  {
                                                      item.attachment.map(( file, index ) => {
                                                          return <div className='file-item'
                                                                      key={index}
                                                                      onClick={() => dowFile(file, file.split('^')[1])}>
                                                              <img
                                                                  src={require("@/assets/images/support/file.png")}/>
                                                              <span>{file.split('^')[1]}</span>
                                                          </div>
                                                      })
                                                  }

                                              </div>
                                              <div
                                                  className='date'>{moment(item.create_time*1000).isSame(moment(), "day") ? '今天 '+moment(item.create_time*1000).format('HH:mm') : moment(item.create_time*1000).format('YYYY-MM-DD HH:mm')}</div>
                                          </div>
                                      } else {
                                          return <div className='item' key={index}>
                                              <div className='name'>
                                                  <img src={require("@/assets/images/support/admin.png")}/>
                                                  {item.admin_name}

                                              </div>
                                              <div className='content' style={{ background: '#F6F7FC' }}>
                                                  <div className='message'>
                                                      {item.content}
                                                  </div>
                                                  <div className='files'>
                                                      {
                                                          item.attachment.map(( file, index ) => {
                                                              return <div className='file-item'
                                                                          key={index}
                                                                          onClick={() => dowFile(file, file.split('^')[1])}>
                                                                  <img
                                                                      src={require("@/assets/images/support/file.png")}/>
                                                                  <span>{file.split('^')[1]}</span>
                                                              </div>
                                                          })
                                                      }

                                                  </div>
                                              </div>
                                              <div
                                                  className='date'>{moment(item.create_time*1000).isSame(moment(), "day") ? '今天 '+moment(item.create_time*1000).format('HH:mm') : moment(item.create_time*1000).format('YYYY-MM-DD HH:mm')}</div>
                                          </div>
                                      }


                                  })
                              }


                          </div>
                      </div>
                      <div className='replies-model'>
                          <TextArea value={msg} onChange={( e ) =>setmessage(e.target.value) }/>
                          <div>
                              <div className='file-list'>
                                  <div style={{'marginBottom':'20px'}}>
                                      <Upload  {...props} showUploadList={{ showPreviewIcon: false }}>
                                          <Button className='upload-btn'>
                                              <img
                                                  src={require("@/assets/images/support/upload-icon.png")}/> {t("support.attachment")}
                                          </Button>
                                          {/*<Progress percent={percent}  />*/}
                                      </Upload>


                                  </div>
                                  {
                                      files.map(( item, index ) => {
                                          if (item.fileSignName === 'img') {
                                              return <div className='img-item' key={index}><img
                                                  width={72}
                                                  height={48}
                                                  src={item.url}/>
                                                  <div className='item-del'
                                                       onClick={() => removeFile(item.name)}>X
                                                  </div>
                                              </div>
                                          } else {


                                              if (item.fileSignName === 'doc') {
                                                  return <div className='other-item' key={index}><img
                                                      src={require("@/assets/images/support/w.png")}/>
                                                      <div className='text'>{item.name}</div>
                                                      <div className='item-del'
                                                           onClick={() => removeFile(item.name)}>X
                                                      </div>
                                                  </div>
                                              } else if (item.fileSignName === 'txt') {
                                                  return <div className='other-item' key={index}><img
                                                      src={require("@/assets/images/support/txt.png")}/>
                                                      <div className='text'>{item.name}</div>
                                                      <div className='item-del'
                                                           onClick={() => removeFile(item.name)}>X
                                                      </div>
                                                  </div>
                                              } else if (item.fileSignName === 'pdf') {
                                                  return <div className='other-item' key={index}><img
                                                      src={require("@/assets/images/support/p.png")}/>
                                                      <div className='text'>{item.name}</div>
                                                      <div className='item-del'
                                                           onClick={() => removeFile(item.name)}>X
                                                      </div>
                                                  </div>
                                              } else if (item.fileSignName === 'rar') {
                                                  return <div className='other-item' key={index}><img
                                                      src={require("@/assets/images/support/RAR.png")}/>
                                                      <div className='text'>{item.name}</div>
                                                      <div className='item-del'
                                                           onClick={() => removeFile(item.name)}>X
                                                      </div>
                                                  </div>
                                              } else if (item.fileSignName === 'xls') {
                                                  return <div className='other-item' key={index}><img
                                                      src={require("@/assets/images/support/xlsx.png")}/>
                                                      <div className='text'>{item.name}</div>
                                                      <div className='item-del'
                                                           onClick={() => removeFile(item.name)}>X
                                                      </div>
                                                  </div>
                                              }

                                          }

                                      })
                                  }
                              </div>
                          </div>
                          <div className='send-btn' style={{ textAlign: 'center', marginTop: '20px' }}>
                              <div className='reply-btn' onClick={ onReplyMessage}>{t("support.send")}</div>
                          </div>
                      </div>
                  </div>
              </Col>
          </Row>

      </div>
      </div>
  )
}
